import React from "react";
import './MyInit.less'
import {Tabs,Typography, Divider} from "antd";
import {SmileTwoTone} from "@ant-design/icons";
const { Title, Paragraph, Text } = Typography;

export default class MyInit extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div className='MyInit'>
                <Tabs defaultActiveKey="1">
                    <Tabs.TabPane tab={<span><SmileTwoTone />我的信息</span>} key="1">
                        <Title level={4}>个人简历</Title>
                        <Paragraph>
                            个人介绍：
                            <pre className='introduce'>
                                <p>毕业学校：盐城师范学院</p>
                                <p>出生日期：1999.1.1</p>
                                <p>自己系统的学习了前端的知识和技能，JavaScript基本功扎实，熟练使用es6的语法，掌握基本的数据结构，会使用React和vue全家桶和使用taro开发跨端小程序。有一年的工作经验，熟悉整个开发到上线流程。</p>
                            </pre>
                        </Paragraph>
                        <Paragraph>
                            技术栈：<Text keyboard>JavaScript(Es6)/css/html</Text>
                            <Text keyboard>Vue全家桶</Text>
                            <Text keyboard>React</Text>
                            <Text keyboard>小程序</Text>
                            <Text keyboard>Node</Text>
                        </Paragraph>
                        <Divider/>
                    </Tabs.TabPane>
                </Tabs>
            </div>
        );
    }
}
